* {
  margin: 0;
  padding: 0;
  overflow: visible;
  box-sizing: border-box;
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 22px;
}

.img {
  background: url(../img/Fon.jpg) no-repeat;
  background-size: cover;
  min-height: 100vh;
}

header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding-top: 48px;
  min-height: 100px;
}

header .center {
  display: inline-block;
  min-width: 30px;
  max-width: 100%;
}

header .logotype {
  margin-right: 100%;
  margin-right: calc(100% - 760px);
  width: 107px;
  font-weight: bold;
}

header .btn {
  position: relative;
  text-decoration: #97c89a;
  color: black;
  margin: 0 0 6px 20px;
  font-weight: bold;
}

header .btn:hover:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid #97c89a;
}

header .btn:nth-child(1) {
  padding: 0;
}

header .btn:nth-child(5) {
  margin: 0 0 0 53px;
}

.main {
  padding-top: 184px;
}

.gradient {
  background: linear-gradient(to top right, #267ebe, #6dd5ed);
}

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
}

.wrapper .main_colortext {
  color: #ffffff;
  margin-bottom: 80px;
  font-size: 60px;
}

.feedback {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.feedback-card {
  width: 372px;
  background-color: #ffffff;
  border-radius: 10px;
  margin-bottom: 45px;
  padding-top: 50px;
  padding: 0 33px;
  padding-bottom: 75px;
}

.feedback-card:nth-child(4) {
  margin-left: 0;
  flex-shrink: 0;
}

.feedback-card__face {
  margin: 68px 0;
}

.feedback-card__text {
  margin: 24px 40px 0 0;
  font-weight: 500;
  margin-bottom: 36px;
}